.title__change {
    display: flex;
    justify-content: center;
    margin: 0;
    color: white;
    font-size: 40px;
    text-shadow: 1px 1px 5px black;
  }
  .contact__name {
    color: white;
    font-size: 25px;
    text-shadow: 1px 1px 5px black;
  }
  .modal__close {
    position: absolute;
    top: 20%;
    left: 82%;
  
    text-decoration: none;
    display: inline-block;
    color: white;
    padding: 10px 20px;
    border: 1px solid #0066ff;
    border-radius: 10px;
    text-transform: uppercase;
    letter-spacing: 2px;
    background-image: linear-gradient(
      to right,
      #33cc33 0%,
      #0066ff 51%,
      #33cc33 100%
    );
    background-size: 200% auto;
    box-shadow: 0 0 20px rgb(0 0 0 / 10%);
    transition: 0.5s;
  }
  .modal__close:hover {
    background-position: right center;
    border-color: white;
  }